// Banner
.banner {
  background: $pink;
  background: url(../img/banner.jpg) 50% 50% no-repeat;
  background-size: cover;
  padding-top: 40px;
  min-height: 550px;
  position: relative;
}

.banner:before,
.banner:after,
.banner-inner:before,
.banner-inner:after {
  content: '';
  width: 25%;
  height: 100%;
  position: absolute;
  top: 0;
  display: block;
  background: $peach;
  z-index: 1000;
}
.banner:before {
  @include animate(.5s);
  animation-delay: .5s;
  animation-name: reveal;
  left: 0;
}
.banner:after {
  @include animate(.5s);
  animation-delay: 1s;
  animation-name: reveal;
  left: 25%;
}
.banner-inner:before {
  @include animate(.5s);
  animation-delay: 1.5s;
  animation-name: reveal;
  left: 50%;
}
.banner-inner:after {
  @include animate(.5s);
  animation-delay: 2s;
  animation-name: reveal;
  left: 75%;
}

.logo {
  width: 250px;
  height: 150px;
  background: url(../img/logo.svg) 50% 50% no-repeat;
  display: block;
  margin: 0 auto 40px auto;
  @include animate(.7s);
  animation-delay: 2.3s;
  animation-name: fadeIn;
  @include breakpoint(m) {
    order: 3;
    margin: 0 30px 0 40px;
  }
}

.navigation {
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  @include breakpoint(m) {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.link-item {
  margin: 0 8px;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  &:after {
    content: '';
    width: 100%;
    height: 2px;
    background: $black;
    display: block;
    transform: scaleX(0);
    transition: all .3s;
  }
  &:hover:after, &.active:after {
    transform: scaleX(1);
  }
  @include breakpoint(m) {
    margin: 0 20px;
  }
}

// Navigation Order
.link-home {
  @include animate(.7s);
  animation-delay: 2.3s;
  animation-name: fadeInRight;
}
.link-about {
  @include animate(.7s);
  animation-delay: 2.3s;
  animation-name: fadeInRight;
}
.link-gallery {
  @include animate(.7s);
  animation-delay: 2.3s;
  animation-name: fadeInLeft;
}
.link-contact {
  @include animate(.7s);
  animation-delay: 2.3s;
  animation-name: fadeInLeft;
}
@include breakpoint(m) {
  .link-home { order: 1; }
  .link-about { order: 2; }
  .link-gallery { order: 4; }
  .link-contact { order: 5; }
}