// Animations

// Reveal
@keyframes reveal {
  0% { transform: scaleY(1); }
  100% { transform: scaleY(0); }
}

// Fade In
@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

// Fade Left
@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translate(-10px, 0);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

// Fade Right
@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translate(10px, 0);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}