// Gallery

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.gallery-heading-block {
  background: $smoke;
  width: 100%;
  padding: 20px;
  @include breakpoint(m) {
    padding: 20px;
    width: 40%;
  }
}
.gallery-item {
  position: relative;
  transition: all .3s;
  width: 50%;
  height: 200px;
  @include breakpoint(m) {
    width: 20%;
    height: 280px;
  }
  &:before,
  &:after {
    content: '';
    position: absolute;
    display: block;
    pointer-events: none;
    transition: all .3s;
  }
  &:before {
    background: url(../img/search.svg) 50% 50% no-repeat;
    top: 50%;
    left: 50%;
    margin-left: -16px;
    margin-top: -16px;
    width: 32px;
    height: 32px;
    z-index: 1000;
    transform: scale(0)
  }
  &:after {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include background-rgba($cream, 0);
  }
  &:hover:before, &:focus:before {
    transform: scale(1);
  }
  &:hover:after, &:focus:after {
    @include background-rgba($cream, .7);
  }
}