// Featured Tweet

.featured-tweet {
  color: $white;
  background: url(../img/tweet-bg.jpg) 50% 50% no-repeat;
  background-size: cover;
  padding: 60px 0;
  position: relative;
  &:after {
    content: '';
    width: 100%;
    height: 100%;
    display: block;
    @include background-rgba($black, .5);
    position: absolute;
    top: 0;
    left: 0;
  }
  @include breakpoint(l) {
    padding: 150px 0;
  }
}

.featured-tweet-content {
  position: relative;
  z-index: 100;
}

.latest-tweet {
  font-size: $base-h3-font-size;
  line-height: $base-h3-line-height;
  font-style: italic;
  font-weight: 700;
  padding: 0 20px;
  @include breakpoint(m) {
    padding: 0 40px;
  }
  @include breakpoint(l) {
    padding: 0 60px;
  }
}

.tweet-heading {
  display: block;
  &:before {
    content: '';
    display: block;
    width: 44px;
    height: 44px;
    margin: 0 auto;
    background: url(../img/tweet.svg) 50% 50% no-repeat;
    background-size: 100% 100%;
  }
}