// Typography
$base-font-family: "Times New Roman", serif;
$base-font-size: 14px;
$base-line-height: 20px;
$base-font-weight: 400;
$base-font-color: #121212;

// Headings
$base-heading-font-family: "Times New Roman", serif;
$base-h1-font-size: 48px;
$base-h1-line-height: 64px;
$base-h2-font-size: 36px;
$base-h2-line-height: 40px;
$base-h3-font-size: 24px;
$base-h3-line-height: 30px;
$base-h4-font-size: 18px;
$base-h4-line-height: 24px;
$base-h5-font-size: 16px;
$base-h5-line-height: 22px;
$base-h6-font-size: 14px;
$base-h6-line-height: 20px;

// Colours
$peach: #e9e0d5;
$pink: #f1b9bc;
$smoke: #cdc8b5;
$darkCream: #9b977a;
$cream: #e7e5d0;
$lightCream: #f3efe6;
$lightGrey: #aeaeae;
$lightestGrey: #f0f0f0;
$black: #000;
$white: #fff;
$dark-grey: #121212;