// Contact
.contact-block {
  background: $cream;
  padding: 30px 0;
  @include breakpoint(m) {
    padding: 60px 0;
  }
  @include breakpoint(l) {
    padding: 100px 0;
  }

  .contact-social {
    margin: 20px 0 40px 0;
    @include breakpoint(m) {
      justify-content: center;
    }
  }
}

.contact-subheading {
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 8px;
  @include breakpoint(m) {
    margin-bottom: 20px;
  }
  @include breakpoint(l) {
    margin-bottom: 30px;
  }
}

.contact-form {
  .input, .textarea {
    border-color: $black;
    &:focus {
      border-color: $darkCream;
    }
  }
}

.contact-button { width: 80%; }

.contact-form-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

.contact-field-group {
  padding-bottom: 30px;
}
.contact-info, .work-hours {
  list-style: none;
  padding: 0;
  margin: 0 0 40px 0;
  @include breakpoint(m) {
    margin: 0;
  }
}
.contact-label {
  text-transform: uppercase;
  display: block;
  margin-top: 8px;
  @include breakpoint(l) {
    display: inline-block;
    margin: 0 8px 0 0;
  }
}
.contact-value {
  font-weight: 700;
}

.map {
  width: 100%;
  overflow: hidden;
  position: relative;
}
.google-map {
  height: 400px;
  width: 100%;
  // filter: grayscale(100%);
  @include breakpoint(m) {
    height: 500px;
  }
  @include breakpoint(l) {
    height: 800px;
  }
  img {
    filter: none;
  }
}