// Global

a { color: $dark-grey; }

// Headings
.heading {
  font-size: $base-h3-font-size;
  line-height: $base-h3-line-height;
  text-transform: uppercase;
  font-weight: 600;
  border: 10px solid $black;
  text-align: center;
  display: block;
  width: 100%;
  padding: 20px;
  margin: 0 auto;
  @include breakpoint(m) {
    width: 300px;
    font-size: $base-h2-font-size;
    line-height: $base-h2-line-height;
  }
}

.heading-block {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.heading-block-inner {
  display: flex;
  align-items: center;
  padding: 20px;
  width: 100%;
  position: relative;
  z-index: 10;
}

.subheading {
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 30px;
}

// Buttons
.button {
  padding: 16px;
  text-transform: uppercase;
  transition: all .3s;
  &:hover, &:focus {
    background: $cream;
    color: $black;
  }
  &:active { top: 1px; }
}
.button-primary {
  color: $white;
  border: 2px solid $darkCream;
  @include background-rgba($darkCream, 1);
  &:hover, &:focus {
    color: $white;
    border: 2px solid $black;
    @include background-rgba($black, 1);
  }
}
.button-secondary {
  border: 1px solid $white;
  @include background-rgba($white, .15);
  &:hover, &:focus { @include background-rgba($white, 0); }
}

.button-full {
  width: 100%;
  display: block;
}

.content {
  background: $smoke;
  padding: 20px;
  height: 100%;
  @include breakpoint(m) {
    padding: 60px 20px;
  }
  @include breakpoint(l) {
    padding: 60px 40px;
  }
}

.content-inner {
  @include breakpoint(m) {
    width: 75%;
    margin: 0 auto;
  }
}

.introduction-copy {
  text-align: center;
  font-size: $base-h4-font-size;
  line-height: $base-h4-line-height;
}

// Images
.image-grid {
  display: flex;
  justify-content: center;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.image-fluid {
  position: absolute;
  top: 50%;
  left: 50%;
  // width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
  @include breakpoint(l) {
    transform: translate(-50%, -50%);
  }
}

// Link
.link {
  text-decoration: none;
  display: inline-block;
  &:after {
    content: '';
    width: 100%;
    height: 1px;
    display: block;
    background: $black;
    transform: scaleX(0);
    transition: all .3s;
  }
  &:hover:after, &:focus:after {
    transform: scaleX(1);
  }
}

// Social Links
.social {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
}
.social-item {
  margin: 25px 25px 0 0;
}
.social-icon {
  width: 24px;
  height: 24px;
  display: block;
  transition: all .3s;
  position: relative;
  cursor: pointer;
  &:before {
    font-family: sans-serif;
    font-size: 11px;
    line-height: 17px;
    position: absolute;
    text-align: center;
    width: 60px;
    top: 0px;
    left: 50%;
    margin-left: -30px;
    transform: translate(0, -200%);
    color: $black;
    padding: 5px;
    background: $cream;
    border-radius: 5px;
    display: block;
    content: attr(title);
    opacity: 0;
    transition: all .3s;
  }
  &:hover, &:focus {
    transform: translate(0, -3px);
    &:before {
      opacity: 1;
      transform: translate(0, -130%);
    }
  }
}
.social-facebook {
  background: url(../img/facebook.svg) 50% 50% no-repeat;
  background-size: 100% 100%;
  &:hover, &:focus {
    background: url(../img/facebook-hover.svg) 50% 50% no-repeat;
    background-size: 100% 100%;
  }
}
.social-twitter {
  background: url(../img/twitter.svg) 50% 50% no-repeat;
  background-size: 100% 100%;
  &:hover, &:focus {
    background: url(../img/twitter-hover.svg) 50% 50% no-repeat;
    background-size: 100% 100%;
  }
}
.social-tumblr {
  background: url(../img/tumblr.svg) 50% 50% no-repeat;
  background-size: 100% 100%;
  &:hover, &:focus {
    background: url(../img/tumblr-hover.svg) 50% 50% no-repeat;
    background-size: 100% 100%;
  }
}
.social-linkedin {
  background: url(../img/linkedin.svg) 50% 50% no-repeat;
  background-size: 100% 100%;
  transform: translate(0, -2px);
  &:hover, &:focus {
    background: url(../img/linkedin-hover.svg) 50% 50% no-repeat;
    background-size: 100% 100%;
  }
}
.social-instagram {
  background: url(../img/instagram.svg) 50% 50% no-repeat;
  background-size: 100% 100%;
  &:hover, &:focus {
    background: url(../img/instagram-hover.svg) 50% 50% no-repeat;
    background-size: 100% 100%;
  }
}
.social-pinterest {
  background: url(../img/pinterest.svg) 50% 50% no-repeat;
  background-size: 100% 100%;
  &:hover, &:focus {
    background: url(../img/pinterest-hover.svg) 50% 50% no-repeat;
    background-size: 100% 100%;
  }
}

// Forms
.input,
.textarea {
  width: 100%;
  padding: 12px;
  border: 3px solid $smoke;
  background: transparent;
  &:focus {
    outline: 0;
    border-color: $black;
  }
}

// Grid
.col-1-2-m {
  @include breakpoint(m) {
    width: 50%;
  }
}
.col-1-3-m {
  @include breakpoint(m) {
    width: 33.3333334%;
  }
}