// Testimonials
.testimonials-block {
  color: $white;
  background: url(../img/testimonials-bg.jpg) 50% 50% no-repeat;
  background-size: cover;
  padding: 60px 0;
  position: relative;
  &:after {
    content: '';
    width: 100%;
    height: 100%;
    display: block;
    @include background-rgba($black, .5);
    position: absolute;
    top: 0;
    left: 0;
  }
  @include breakpoint(l) {
    padding: 150px 0;
  }
}

.testimonial {
  position: relative;
  z-index: 100;
  padding: 50px 0;
  @include breakpoint(m) {
    padding: 0 150px;
  }
  &:before, &:after {
    content: "â€œ";
    font-size: 150px;
    line-height: 160px;
    display: block;
    width: 32px;
    height: 32px;
    position: absolute;
  }
  &:before {
    left: 0;
    top: -50px;
    transform: rotate(0deg);
  }
  &:after {
    right: 0;
    bottom: -50px;
    transform: rotate(180deg);
  }
}
.testimonial-content {
  font-size: $base-h3-font-size;
  line-height: $base-h3-line-height;
  font-style: italic;
  font-weight: 700;
  padding: 0 20px;
  @include breakpoint(m) {
    padding: 0 40px;
  }
  @include breakpoint(l) {
    padding: 0 60px;
  }
}

.testimonial-author {
  display: block;
  text-transform: uppercase;
  text-align: right;
  &:before, &:after {
    content: '-';
    display: inline-block;
    margin: 0 4px;
  }
}