// About
.about-heading-block {
  background: $cream;
  min-height: 200px;
  background: url(../img/about.jpg) 50% 50% no-repeat;
  background-size: cover;
  position: relative;
  &:after {
    content: '';
    width: 100%;
    height: 100%;
    display: block;
    @include background-rgba($cream, .4);
    position: absolute;
    top: 0;
    left: 0;
  }
  @include breakpoint(m) {
    min-height: auto;
  }
}

.tattoo-design:before {
  content: '';
  width: 100px;
  height: 100px;
  background: url(../img/tattoo-design.png) 50% 50% no-repeat;
  display: block;
  margin: 0 auto;
}

.collage {
  display: flex;
  flex-wrap: wrap;
}
.collage-item {
  width: 20%;
  height: 100px;
  @include breakpoint(l) {
    height: 200px;
  }
}